import React from 'react';
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component{
  state={
    isLoading: true,
    movies: []
  };
  getMovies = async () => {
    const {data: {data: {movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json?sort_by=rating");
    this.setState({movies : movies, isLoading:false})
    //state's movies : axios's movies
    //this.setState({movies}) is also possible!
  }
  componentDidMount(){
    //fetch data here with axios
    this.getMovies();
  }
  render(){
    const { isLoading , movies }=this.state;
    return(
      //looks like HTML but it's JSX (eventually JS)
      //in JS class means class like App
      //to avoid ambiguity need to use className here
      <section className="container">
         {isLoading
         ? (<div className="loader">
              <span className="loader__text">Loading...</span>
            </div>)
         : (<div className="movies">
              {movies.map( movie => (
                <Movie
                  key={movie.id} 
                  id={movie.id}  
                  year={movie.year} 
                  title={movie.title} 
                  summary={movie.summary} 
                  poster={movie.medium_cover_image}
                  genres={movie.genres} />)
                  //map must return something!!
                  //inside ={} should be the real attirbute name in the object. not the name we made.
                  )
              }
            </div>)
            
        }
      </section>
    )
  }
}

export default App;
